import fetch from '@/plugin/axios'
import fetch1 from '@/plugin/axios/deviceAxios'
import fetch2 from '@/plugin/axios/setAxios'

/**
 *  
 * @param {*} query
 */
export function fetchGetInstanceList(query) {
  return fetch1({
    url: '/nacos/v1/ns/instance/list',
    method: 'get',
    params: query
  })
}
/**
 *  
 * @param {*} query
 */
export function fetchGetServiceList(query) {
  return fetch1({
    url: '/nacos/v1/ns/service/list',
    method: 'get',
    params: query
  })
}
/**
 *  
 * @param {*} query
 */
export function fetchDeleteInstance(query) {
  return fetch1({
    url: '/nacos/v1/ns/instance',
    method: 'delete',
    params: query
  })
}
/**
 *  
 * @param {*} query
 */
export function fetchAddInstance(parmas) {
  return fetch1({
    url: '/nacos/v1/ns/instance',
    method: 'post',
    data: parmas
  })
}
/**
 *  
 * @param {*} query
 */
export function fetchUpdateInstance(parmas) {
  return fetch1({
    url: '/nacos/v1/ns/instance',
    method: 'put',
    data: parmas
  })
}

