import {
  fetchGetServiceList,
  fetchGetInstanceList
} from "@/api/demo/index";
 
export default {
  namespaced: true,
  state: {
    serviceName: '',
    instenceName: {},
    serviceList: [],
    instenceList: []
  },
  actions: {
    commitServiceList ({ commit, dispatch }) {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          let serviceArray = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (serviceArray.length > 0) {
            commit('setServiceList', serviceArray)
            commit('setServiceName', serviceArray[0])
            dispatch("commitInstenceList")
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "", {
            confirmButtonText: "",
            callback: (action) => {},
          });
        });
    },
    commitInstenceList({ state, commit }) {
      if (state.serviceName === 'upf' && state.instenceList.length > 0) {
        commit('setInstenceName', instancedata[0])
        return
      }
      fetchGetInstanceList({ serviceName: state.serviceName })
        .then((response) => {
          console.log(response);
          let instancedata = response.hosts ? response.hosts : []
          commit('setInstenceList', instancedata)
          if (instancedata.length > 0) {
            commit('setInstenceName', instancedata[0])
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "", {
            confirmButtonText: "",
            callback: (action) => {},
          });
        });
    }
  },
  mutations: {
    setServiceName(state, serviceName) {
      state.serviceName = serviceName
    },
    setInstenceName(state, instenceName) {
      state.instenceName = instenceName
    },
    setServiceList(state, serviceList) {
      state.serviceList = serviceList
    },
    setInstenceList(state, instenceList) {
      state.instenceList = instenceList
    }
  }
}
